.main {
  border-radius: 20px;
  width: 80%;
  padding: 20px;
  background-color: aliceblue;
}

.gridBox {
  display: grid;
  grid-template-columns: 250px 1fr;
  grid-template-rows: 1fr;
  column-gap: 20px;
  row-gap: 20px;
}

.articleText {
  text-align: left;
}

.summary {
  grid-column: 1/3;
  grid-row: 2/3;
  text-align: left;
}

.summary :first-child {
  margin: 0;
}

@media (max-width: 700px) {
  .gridBox :first-child {
    grid-column: 1/3;
    justify-self: center;
  }
  .summary {
    grid-column: 1/3;
    grid-row: 3/4;
  }
  .trailerBox {
    grid-column: 1/3;
    grid-row: 4/5;

    position: relative;
    width: 100%;
    padding-bottom: 56.25%;
  }

  .trailer {
    position: absolute;
    transform: translateX(-50%);
    width: 100%;
    height: 100%;
  }

  .articleText {
    grid-column: 1/3;
    grid-row: 2/3;
  }
}
